import { Container, Typography, Button, TextField, Checkbox, FormControlLabel, FormControl, FormHelperText, Radio, FormLabel, RadioGroup } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useState, useRef } from 'react';
import OstosKori from './OstosKori';

const useStyles = makeStyles({
  kappale : {
                    marginBottom : "10px",
                    marginTop : "10px"
                 }              
});

function TilausLomake() {

  const tyyleja = useStyles();

  const lomaketiedot = useRef({});
  const [virhe, setVirhe] = useState({});
 
  const lomakeKasittelija = (e) => {

    e.preventDefault();

    let virheet = {};

    if (!lomaketiedot.current.nimi) {

      virheet = {...virheet, nimi : "Nimi puuttuu"}
      
    } 
    
    if (!lomaketiedot.current.sukunimi) {

        virheet = {...virheet, sukunimi : "Sukunimi puuttuu"}
        
      } 

      if (!lomaketiedot.current.kotiosoite) {

        virheet = {...virheet, kotiosoite : "Kotiosoite puuttuu"}
        
      } 

      if (!lomaketiedot.current.puhnumero) {

        virheet = {...virheet, puhnumero : "Puhelinumero puuttuu"}
        
      } 

    if (!lomaketiedot.current.email) {

      virheet = {...virheet, email : "Sähköpostiosoite puuttuu"}
      
    } else {

      if (lomaketiedot.current.email.search("@") === -1) {

        virheet = {...virheet, email : "Virheellinen sähköpostiosoite"}
      }

    }

    if (!lomaketiedot.current.ehdot) {

      virheet = {...virheet, ehdot : "Sinun täytyy täyttää kaikki kohdat"}
      
    } 

    if (!lomaketiedot.current.kysymys) {

      virheet = {...virheet, kysymys : "Valitse joku kysymyksen vastauksesta"}
      
    } 

    if (Object.entries(virheet).length > 0) {

      setVirhe({...virheet});

    } else {

      setVirhe({});

      window.alert("Olet tilannut meiltä, me kiitämme!");

    }

    

  }

  const syoteKasittelija = (e) => {

    let arvo = (e.target.type === "checkbox") ? e.target.checked : e.target.value;

    lomaketiedot.current[e.target.name] = arvo;

  }

  return (
    <Container maxWidth="sm">
      
    <Typography variant="h5">Tilauslomake</Typography>

    

    <form onSubmit={lomakeKasittelija}>

    <TextField 
        name="nimi"
        variant="outlined"
        label="Nimi"
        fullWidth={true}
        className={tyyleja.kappale}
        error={Boolean(virhe.nimi)}
        helperText={virhe.nimi}
        onChange={ syoteKasittelija }
      />

    <TextField 
        name="sukunimi"
        variant="outlined"
        label="Sukunimi"
        fullWidth={true}
        className={tyyleja.kappale}
        error={Boolean(virhe.sukunimi)}
        helperText={virhe.sukunimi}        
        onChange={ syoteKasittelija }
      />

    <TextField 
        name="email"
        variant="outlined"
        label="Sähköposti"
        fullWidth={true}
        className={tyyleja.kappale}
        error={Boolean(virhe.email)}
        helperText={virhe.email}        
        onChange={ syoteKasittelija }
      />

    <TextField 
        name="kotiosoite"
        variant="outlined"
        label="Kotiosoite"
        fullWidth={true}
        className={tyyleja.kappale}
        error={Boolean(virhe.kotiosoite)}
        helperText={virhe.kotiosoite}        
        onChange={ syoteKasittelija }
      />

    <TextField 
        name="puhnumero"
        variant="outlined"
        label="Puhelinnumero"
        fullWidth={true}
        className={tyyleja.kappale}
        error={Boolean(virhe.puhnumero)}
        helperText={virhe.puhnumero}        
        onChange={ syoteKasittelija }
      />

      
      <FormControl fullWidth style={{marginTop : 10}}>
       <FormLabel>Kuinka usein käyt sivustollamme?</FormLabel>
        <RadioGroup>
          <FormControlLabel 
            value="paiva" 
            label="Päivittäin"
            control={<Radio 
                        name="kysymys"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="viikko" 
            label="Viikoittain"
            control={<Radio 
                        name="kysymys"  
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
          <FormControlLabel 
            value="kuukausi" 
            label="Kuukausittain"
            control={<Radio 
                        name="kysymys" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
             <FormControlLabel 
            value="vuosittain" 
            label="Vuosittain"
            control={<Radio 
                        name="kysymys" 
                        size="small"
                        onChange={ syoteKasittelija } 
                    />} 
          />
        </RadioGroup>
        <FormHelperText error={Boolean(virhe.kysymys)}>{virhe.kysymys}</FormHelperText>      
      </FormControl>

      <FormControl>
        <FormControlLabel 
          control={<Checkbox 
                      name="ehdot" 
                      onChange={ syoteKasittelija }/>}
          label="Antamani tiedot ovat oikein"
        />
        <FormHelperText 
          error={Boolean(virhe.ehdot)}
        >{virhe.ehdot}</FormHelperText>
      </FormControl>
      
      <Button 
        type="submit"
        variant="contained" 
        color="primary"
        fullWidth={true}
        size="large"
      >
        Tilaa
      </Button>

      <OstosKori></OstosKori>

      </form>

    </Container>
  );
}

export default TilausLomake;