import {useRef} from 'react';
import { TextField, Button, Grid, FormControl } from "@material-ui/core";


function Palaute() {


  const formEl = useRef(null);

  const submitForm = e => {
      
    const formData = new FormData(formEl.current);
    const nimi = formData.get("nimi");
    const sposti = formData.get("sposti");
    const viesti = formData.get("viesti");
    console.log(nimi, sposti, viesti);
    
    e.preventDefault();

  }

  return (
      <FormControl>
    <Grid
      component="form"
      ref={formEl}
      onSubmit={submitForm}
      container
      required
      justify="center"
      style={{ width: "90%", maxWidth: "500px", margin: "0 auto" }}
    >
      <TextField
        id="outlined-email-input"
        label="Nimi"
        type="text"
        name="nimi"
        autoComplete="nimi"
        margin="normal"
        variant="outlined"
        fullWidth
        color="primary"
        required
      />
      <TextField
        id="outlined-email-input"
        label="Sähköposti"
        type="sposti"
        name="sposti"
        autoComplete="sposti"
        margin="normal"
        variant="outlined"
        fullWidth
        color="primary"
        required
      />
      <TextField
        id="outlined-email-input"
        label="Palaute"
        multiline
        rows="4"
        type="text"
        name="message"
        autoComplete="feedback"
        margin="normal"
        variant="outlined"
        fullWidth
        color="primary"
        required
      />
      <br/>
      <Button
        variant="outlined"
        color="primary"
        onClick={ (submitForm) => {alert("Kiitos kun annoit palautetta!")
    }}
        type="submit"
      >
        Lähetä
      </Button>
    </Grid>
   </FormControl>
  );

};

export default Palaute;
