import { Button, TextField, FormControlLabel, Checkbox, Container, Typography} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';



const useStyles = makeStyles({
    tekstikentta :{
                    marginBottom:"10px",
                    marginTop:"3px",
                  },
    media :{
                    height:"50",
                    width:"40",
                  },
    paaotsikko: {
                    marginTop : 10,
                    fontSize : 22
                },
    alaotsikko : {
                    marginTop : 10,
                    fontSize : 18
                },
    kappale : {
                    marginTop : 10,
                    fontSize : 14
                }
                });

function Kirjaudu(){

const tyyleja = useStyles();

const[kirjaudu, setKirjaudu] = useState(false);
const[kayttaja, setKayttaja] = useState("");
const[salasana, setSalasana] = useState("");
const[muista, setMuista] = useState(false);


useEffect( () => { 

if(kayttaja && salasana && muista){
  setKirjaudu(true); 
}
else{
  setKirjaudu(false);
}

}, [kayttaja, salasana, muista] );

    return(

<Container flex>
<Typography className={tyyleja.paaotsikko}>Kirjaudu</Typography>
 <TextField 
  label="Käyttäjätunnus" 
  placeholder= "Käyttäjätunnus"
  variant="outlined" 
  size="small"
  className={tyyleja.tekstikentta}
  onChange={ (e) => {setKayttaja(e.target.value) } }
/>
<br>
</br>

<TextField
type="pwd"
  label="Salasana" 
  placeholder= "Salasana"
  variant="outlined" 
  size="small"
  className={tyyleja.tekstikentta}
  onChange={ (e) => {setSalasana(e.target.value) } }
/>

<br>
</br>

<FormControlLabel
  control={<Checkbox onChange={ (e) => {setMuista(e.target.checked) } }/>}
  label="Muista minut"
  size="small"
/>

<Button 
  variant="contained" 
  color="default" 
  size="small"
  disabled={!kirjaudu}
  component={Link}
          to="/omasivu"
> Kirjaudu </Button>

</Container>
    );
}
export default Kirjaudu
