import { useState } from "react";
import data from "./data";
import SingleQuestion from "./KysymysInfo";
import { Container } from "@material-ui/core";




function Kysymyksia() {

 


  const [asiat] = useState(data);

  return (
    <main>
      <Container
      style={{
        fontSize:25

      }}
      >
        <h3>Ihmisten kysymyksiä</h3>
        <section className="info">
          {asiat.map((kysymys) => {
            return <SingleQuestion key={kysymys.id} {...kysymys} />;
          })}
        </section>
      </Container>
      
    </main>
  );
}

export default Kysymyksia;
