import Aloitus from './components/Aloitus';
import Navigointi from './components/Navigointi'
import Footer from './components/Footer';
import Kysymyksia from './components/Kysymyksia';
import TilausLomake from './components/TilausLomake';
import Tuotteet from './components/Tuotteet';
import Yhteystiedot from './components/Yhteystiedot'
import Kirjaudu from './components/Kirjaudu'
import OmaSivu from './components/OmaSivu'
import Palaute from './components/Palaute'
import {BrowserRouter as Router, Route} from 'react-router-dom';
import { useState} from 'react';



function App() {
  const[tilaus, setTilaus] = useState();
  const[sivut, setSivut] = useState();

 
  return (
    <Router>
     
    <Navigointi />
      <Route path="/" exact component={Aloitus}/>
      <Route path="/Tuotteet" component={Tuotteet}/>
      <Route path="/TilausLomake" component={TilausLomake}/>
      <Route path="/KysymysInfo" component={Kysymyksia}/>
      <Route path="/Yhteystiedot" component={Yhteystiedot}/>
      <Route path="/Palaute" component={Palaute}/>
      <Route path="/Kirjaudu" component={Kirjaudu}/>
      
      <Route path="/omasivu" >
          <OmaSivu sivut={sivut} setSivut={setSivut} />
        </Route>
      <Route path="/tilaus">
          <TilausLomake tilaus={tilaus} setTilaus={setTilaus}/>
        
        </Route>
      

      
      <Footer>Albus - Kaverina myötä- ja vastakarvassa | Albus - Copyright © 2021 Albus Oy</Footer>
    </Router>


    
  );
}

export default App;